.advance-table {
  position: relative;
  > button {
    position: absolute;
    top: 8px;
    right: 8px;
    z-index: 10;
  }
}
.react-resizable {
  position: relative;
  background-clip: padding-box;
}

.react-resizable-handle {
  position: absolute;
  right: -5px;
  bottom: 0;
  z-index: 1;
  width: 10px;
  height: 100%;
  cursor: col-resize;
}

